/**=====================
      08. GRID CSS Start
==========================**/
.grid-showcase {
  margin-bottom: -10px;

  span {
    display: block;
    border: 1px solid $light-semi-gray;
    padding: 10px;
    margin-bottom: 10px;
    text-align: center;
    background-color: rgba($primary-color, 0.1);
    color: $primary-color;
  }
}

.grid-align {
  margin-bottom: -30px;

  .row {
    padding: 15px 0;
    min-height: 10rem;
    border: 1px solid rgba($primary-color, 0.08);
    margin: 0px 3px 30px;
    background-color: rgba($primary-color, 0.08);
  }
}

/**=====================
      08. GRID CSS Ends
==========================**/