/**=====================
     31. JS datagrid CSS Start
==========================**/
.jsgrid {
  .jsgrid-grid-body {
    .jsgrid-align-right {
      text-align: center;
    }

    .jsgrid-cell {
      padding: 0.75rem;
    }
  }

  .jsgrid-pager-container {
    text-align: right;
  }

  .jsgrid-pager {
    padding: 0;
    margin-top: 20px;

    .jsgrid-pager-nav-inactive-button {
      margin-right: -5px;

      &:first-child {
        border-radius: 5px 0 0 5px;
      }
    }

    .jsgrid-pager-page {
      &~.jsgrid-pager-nav-button {
        margin-left: -5px;

        &:last-child {
          border-radius: 0 5px 5px 0;
        }
      }
    }

    [class*="jsgrid-pager"] {
      display: inline-block;
      min-width: 1.5em;
      padding: 0.5em 1em;
      border: 1px solid $light-color;
    }

    .jsgrid-pager-page {
      a {
        color: $dark-color;
      }
    }

    .jsgrid-pager-current-page {
      color: $dark-color;
    }
  }

  .jsgrid-selected-row {
    >.jsgrid-cell {
      background-color: rgba($primary-color, 0.1);
      border-color: $auth-bg-color;
    }
  }

  .jsgrid-header-row {
    >.jsgrid-header-cell {
      padding: 0.75rem;
    }
  }
}

.jsgrid-filter-row {

  >.jsgrid-cell {
    border: 1px solid $light-semi-gray;
    border-bottom: 0;
    background-color: rgba($primary-color, 0.07);
  }

  input {
    width: 100%;
    padding: .3em .5em;
    background-color: $white;
    border: none;
    box-shadow: none;
    outline: none;
  }

  select {
    width: 100%;
    padding: .3em .5em;
    background-color: $white;
    border: none;
    box-shadow: none;
    outline: none;
  }
}

.jsgrid-header-row {
  >.jsgrid-header-cell {
    background-color: $white;
    border-color: $light-semi-gray;
  }
}

.jsgrid-alt-row {
  >.jsgrid-cell {
    background-color: rgba($primary-color, 0.07);
  }
}





/**=====================
     31. JS datagrid CSS Ends
==========================**/