/**===============================
     62. Responsive CSS Start
==================================**/

/* ========= min and max scss for 1200 to 1366 screen ========= */
@media screen and (max-width: 1660px) {
  .photos {
    ul {
      li {
        width: 22%;
      }
    }
  }

  .vertical-menu-main {
    .mega-menu {
      width: 1200px !important;
      max-width: 1200px !important;
      left: 0px !important;
    }
  }

  .bar-chart {
    canvas {
      width: 100% !important;
    }
  }

  .main-header-right {
    .main-header-right {
      .left-menu-header {
        .orm-control-plaintext {
          .form-inline {
            width: auto !important;
          }
        }
      }
    }
  }

  // blog
  .blog-list {
    .blog-details {
      p {
        width: 100%;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
      }
    }
  }

  // chat
  .chat-box {
    .chat-history {
      .call-icons {
        margin-bottom: 15px;

        ul {
          li {
            width: 45px;
            height: 45px;

            a {
              font-size: 18px;
            }
          }
        }
      }

      .total-time {
        h2 {
          font-size: 40px;
          color: $dark-gray;
          margin-bottom: 10px;
        }
      }

      .call-content {
        .btn {
          width: auto;
          margin: 0 auto;
        }
      }
    }
  }

  .chat-left-aside {
    .people-list {
      height: 520px;
    }
  }

  .chat-box {
    .chat-right-aside {
      .chat {
        .chat-msg-box {
          height: 440px;
        }
      }
    }

    .people-list {
      ul {
        max-height: 450px;
        overflow: auto;
      }
    }
  }

  .call-chat-body {
    .chat-box {
      .people-list {
        ul {
          max-height: 500px;
          overflow: auto;
        }
      }

      overflow: hidden;
      height: 625px;
    }
  }

  .caller-img {
    img {
      opacity: 0.7;
    }
  }

  .chat-box {
    .chat-history {
      .receiver-img {
        margin-top: 25px;
      }

      .call-content {
        min-height: 545px;
      }
    }
  }

  // search //
  .search-page {
    .search-vid-block {
      >div {
        margin-top: 30px;
      }
    }
  }

  // user profile //
  .user-profile {
    .photos {
      ul {
        li {
          width: 21%;
        }
      }
    }
  }

  // timeline small page
  .timeline-small {
    .media {
      .timeline-round {
        &.timeline-line-1 {
          &:after {
            bottom: -78px;
          }
        }
      }
    }
  }

  // general widget //
  .user-status {
    table {
      thead {
        tr {
          th {
            &:nth-child(3) {
              min-width: 124px;
            }
          }
        }
      }

      tbody {
        tr {
          td {
            &:first-child {
              min-width: 278px;
            }
          }
        }
      }
    }
  }

  .employee-status {
    table {
      tbody {
        tr {
          td {
            &:first-child {
              min-width: 300px;
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 1550px) {
  .bg-gallery {
    height: 90vh;
  }

  // ecommerce
  .grid-options {
    ul {
      li {
        &:last-child {
          display: none;
        }
      }
    }
  }

  .product-page-main {
    .pro-slide-right {
      img {
        height: 122px;
      }
    }
  }

  // email application
  .email-wrap .email-content .email-top .user-emailid:after {
    display: none;
  }

  .email-wrap .email-top .email-right {
    display: none;
  }

  .cal-date-widget {
    .datepicker {
      width: unset;
    }
  }
}

@media screen and (max-width: 1470px) {

  // user profile
  .edit-profile {
    .profile-title {
      .media {
        display: block;

        .media-body {
          margin-left: unset;
          margin-top: 15px;
        }
      }
    }
  }

  .user-profile {
    .photos {
      ul {
        li {
          width: 20%;
        }
      }
    }
  }
}

@media screen and (max-width: 1440px) {
  .bg-gallery {
    height: 85vh;
    min-height: 700px;
  }

  // ecommerce //
  .filter-section {
    .left-filter {
      .card-body {
        padding: 20px;

        .product-price {
          font-size: 14px;
          font-weight: 400;
        }
      }
    }
  }

  .product-filter {
    &.new-products {
      .owl-theme {
        .owl-item {
          .item {
            .product-box {
              .product-details {
                .fa-star {
                  margin-right: 0 !important;
                }
              }
            }
          }
        }
      }
    }
  }

  // email wrap
  .email-wrap {
    .email-right-aside {
      .email-profile {
        .inbox {
          p {
            max-width: 420px;
          }
        }
      }
    }
  }
}

@media (max-width: 1280px) {
  .bg-gallery {
    height: 108vh;
  }

  .img-gallery-set1,
  .img-gallery-set5 {
    display: none;
  }

  .box-layout.page-wrapper.horizontal-wrapper {
    .page-main-header {
      padding-left: unset;
      max-width: unset;
      left: unset;
    }

    .page-body-wrapper {
      width: unset;
    }
  }

  // emaill application
  .email-wrap {
    .actions {
      li {
        &:nth-child(n + 2) {
          margin-left: 10px;
        }

        .btn {
          padding: 7px 20px;
          text-transform: capitalize;
        }
      }
    }
  }

  // user profile
  .user-profile {
    .photos {
      ul {
        li {
          width: 19%;
        }
      }
    }

    .social-tab {
      ul {
        li {
          &:nth-child(n + 2) {
            margin-left: 10px;
          }
        }
      }
    }
  }

  // blog
  .blog-list .blog-details {
    hr {
      display: none;
    }

    p {
      display: none;
    }
  }
}

