/**=====================
   55. theme layout CSS Start
==========================**/
//Sidebar color scss
$color1: #405365;
$color2: #1c1d1e;
$color3: #0c0c3c;
$color4: #0747a6;
$color5: #4d3096;

@each $bg-name,
$bg-color in (color1, $color1),
(color2, $color2),
(color3, $color3),
(color4, $color4),
(color5, $color5),
(light, $light-color),
(dark, #2f3c4e) {
  .bg-#{$bg-name} {
    background-color: $bg-color;
  }
}


//Sidebar settings
.page-wrapper.compact-wrapper,
.page-wrapper.horizontal-wrapper {
  .page-body-wrapper {
    &.horizontal-menu {
      header {
        &.main-nav {
          &.dark-sidebar {
            background: $dark-color;

            .nav-menu {
              >li {
                .nav-submenu {
                  li {
                    a {
                      &:after {
                        background: rgba($white, 0.8);
                      }
                    }
                  }
                }

                .nav-link {
                  &.active {
                    color: $white;
                  }
                }

                >a {
                  color: $dark-all-font-color;
                }
              }

              &.custom-scrollbar {
                &::-webkit-scrollbar-thumb {
                  background-color: $dark-card-background;
                }
              }

              .dropdown {
                ul.nav-submenu {
                  li {
                    a {
                      color: $dark-all-font-color;

                      &.active {
                        color: $primary-color;
                      }
                    }
                  }
                }
              }
            }

            .logo-wrapper {
              img {
                filter: brightness(0) invert(1);
              }
            }

            .main-navbar {
              .nav-menu {
                &>li {
                  .nav-submenu {
                    &:after {
                      background-color: $light-all-font-color;
                    }
                  }

                  .mega-menu-container {
                    .mega-box {
                      .link-section {
                        .submenu-title {
                          h5 {
                            color: $dark-all-font-color;

                            &::after {
                              background-color: $dark-card-background;
                            }
                          }
                        }

                        .submenu-content {
                          &.opensubmegamenu {
