//Dark layout
body {
  .main-nav[sidebar-layout="border-sidebar"] {
    .main-navbar {
      .nav-menu {
        >li {
          .nav-link {
            border-bottom: 1px solid $light-gray;
            transition: all 0.3s ease;
          }
        }
      }
    }
  }


  .page-wrapper {
    &.horizontal-wrapper {
      .page-body-wrapper {
        .horizontal-menu header {
          &.main-nav[sidebar-layout="iconcolor-sidebar"] {
            .main-navbar {
              .nav-menu {
                >li {
                  $colors: $success-color,
                    $secondary-color,
                    $primary-color,
                    $danger-color,
                    $info-color,
                    $warning-color,
                    $light-color;
                  $repeat: 100;

                  @for $i from 1 through $repeat {
                    &:nth-child(#{length($colors)}n + #{$i}) {
                      svg {
                        color: lighten(nth($colors, random(length($colors))),
                            10%) !important;
                      }

                      a {
                        &:hover {
                          >span {
                            color: lighten(nth($colors, random(length($colors))),
                                15%);
                          }
                        }
                      }
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }



  .page-wrapper {
    &.compact-wrapper {
      .page-body-wrapper {
        header {
          &.main-nav[sidebar-layout="iconcolor-sidebar"] {
            .main-navbar {
              .nav-menu {
                >li {
                  $colors: $success-color, $secondary-color, $primary-color,
                    $danger-color, $info-color, $warning-color, $light-color;
                  $repeat: 100;

                  @for $i from 1 through $repeat {
                    &:nth-child(#{length($colors)}n + #{$i}) {
                      svg {
                        color: lighten(nth($colors, random(length($colors))),
                            10%) !important;
                      }

                      a {
                        &:hover {
                          >span {
                            color: lighten(nth($colors, random(length($colors))),
                                15%);
                          }
                        }
                      }
                    }
                  }
                }
              }

              &.light-sidebar {
                .main-navbar {
                  .nav-menu {
                    >li {
                      $colors: $success-color, $secondary-color, $primary-color,
                        $danger-color, $info-color, $warning-color, $dark-color;
                      $repeat: 100;

                      @for $i from 1 through $repeat {
                        &:nth-child(#{length($colors)}n + #{$i}) {
                          svg {
                            color: darken(nth($colors, random(length($colors))),
                                15%);
                          }

                          a {
                            &:hover {
                              span {
                                color: darken(nth($colors, random(length($colors))),
                                    15%);
                              }
                            }
                          }
                        }
                      }
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }

  &.light-only {
    .page-wrapper.horizontal-wrapper {
      .page-body-wrapper {
        .page-body {
          background-color: $light;

          ul.the-icons {
            li {
              color: $black;
              display: inline-block;
              padding: 10px;

              &:hover {
                background: $black;
                box-shadow: 0 0 3px $dark-card-background;
                color: $white;
              }

              em {
                display: none;
              }
            }
          }
        }
      }
    }
  }

  &.dark-only {
    color: $dark-all-font-color;
    background-color: $dark-body-background;

    .user-profile {
      .post-about {
        ul {
          li {
            h5 {
              color: rgba(255, 255, 255, 0.8);
            }
          }
        }
      }

      .profile-post {
        .post-body {
          .post-react {
            h6 {
              color: rgba(255, 255, 255, 0.8);
            }
          }

          .post-comment {
            li {
              label {
                a {
                  color: #aaaaaa;
                }
              }
            }
          }
        }
      }
    }



    .project-calendar {
      &.card {
        .card-header {
          border-bottom-color: $dark-card-border;
        }
      }
    }

    .blog-list {
      .blog-details {
        h6 {
          color: rgba(255, 255, 255, 0.8);
        }
      }
    }

    .form-detail {
      .new-task-wrapper {
        textarea {
          background-color: #262932;
          border-color: #374558;
          color: rgba(255, 255, 255, 0.8);
          border-radius: 5px;
          outline: none;
        }

      }
    }

    .features-faq {
      .card-footer {
        border-top: 1px solid #515558;
      }
    }

    span.twitter-typeahead {

      .tt-menu,
      .tt-suggestion {
        background-color: $dark-body-background;
        border-top: none;
      }
    }

    .main-nav[sidebar-layout="border-sidebar"] {
      .main-navbar {
        .nav-menu {
          >li {
            .nav-link {
              border-bottom: 1px solid $dark-card-border;
              transition: all 0.3s ease;
            }
          }
        }
      }
    }

    .main-nav[sidebar-layout="iconcolor-sidebar"] {
      .main-navbar {
        .nav-menu {
          >li {
            $colors: $success-color, $secondary-color, $primary-color,
              $danger-color, $info-color, $warning-color, $light-color;
            $repeat: 100;

            @for $i from 1 through $repeat {
              &:nth-child(#{length($colors)}n + #{$i}) {
                svg {
                  color: lighten(nth($colors, random(length($colors))),
                      15%) !important;
                }

                a {
                  &:hover {
                    >span {
                      color: lighten(nth($colors, random(length($colors))),
                          15%);
                    }
                  }
                }
              }
            }
          }
        }
      }

      &.light-sidebar {
        .main-navbar {
          .nav-menu {
            >li {
              $colors: $success-color, $secondary-color, $primary-color,
                $danger-color, $info-color, $warning-color, $dark-color;
              $repeat: 100;

              @for $i from 1 through $repeat {
                &:nth-child(#{length($colors)}n + #{$i}) {
                  svg {
                    color: darken(nth($colors, random(length($colors))), 15%);
                  }

                  a {
                    &:hover {
                      span {
                        color: darken(nth($colors, random(length($colors))),
