  &.dark-sidebar {
    .page-wrapper {
      &.compact-wrapper {
        .page-body-wrapper {
          header {
            &.main-nav {
              background-color: $dark-card-background;

              .nav-menu {
                >li {
                  .nav-submenu {
                    li {
                      a {
                        &:after {
                          background: $dark-card-background;
                        }
                      }
                    }
                  }

                  .mega-menu-container {
                    .mega-box {
                      .link-section {
                        .submenu-title {
                          h5 {
                            color: $dark-all-font-color;

                            &:after {
                              background-color: $dark-card-background;
                            }
                          }
                        }

                        .submenu-content {
                          &.opensubmegamenu {
                            li {
                              a {
                                color: $dark-all-font-color;
                              }
                            }
                          }
                        }
                      }
                    }

                    &::after {
                      background-color: $light-all-font-color;
                    }
                  }

                  .nav-link {
                    &.active {
                      color: $white;
                    }
                  }

                  >a {
                    color: $dark-all-font-color;
                  }
                }

                &.custom-scrollbar {
                  &::-webkit-scrollbar-thumb {
                    background-color: $dark-card-background;
                  }
                }

                .dropdown {
                  ul.nav-submenu {
                    li {
                      a {
                        color: $dark-all-font-color;

                        &.active {
                          color: $primary-color;
                        }
                      }
                    }
                  }
                }
              }

              .logo-wrapper {
                background-color: $dark-card-background;

                img {
                  filter: brightness(0) invert(1);
                }
              }

              .main-navbar {
                .nav-menu {
                  >li {
                    .nav-submenu {
                      &:after {
                        background-color: $light-all-font-color;
                      }
                    }

                    a {
                      svg {
                        color: $dark-all-font-color !important;
                      }
                    }

                    .nav-link {
                      &.active {
                        background-color: $dark-card-background;
                      }
                    }
                  }
                }
              }
            }
          }

          .according-menu {
            i {
              color: $dark-all-font-color;
            }
          }
        }
      }

      &.horizontal-wrapper {
        .page-body-wrapper {
          &.horizontal-menu {
            header {
              &.main-nav {
                background: $dark-card-background;

                .main-navbar {
                  .nav-menu {
                    >li {
                      >a {
                        color: $dark-all-font-color;
                      }

                      .nav-submenu {
                        background: $dark-card-background;

                        li {
                          a {
                            color: $dark-all-font-color;

                            &.active,
                            &:hover {
                              color: $primary-color;
                            }
                          }

                          .nav-sub-childmenu {
                            background: $dark-card-background;
                          }
                        }
                      }

                      .mega-menu-container {
                        background: $dark-card-background;

                        .mega-box {
                          .link-section {
                            .submenu-content {
                              li {
                                a {
                                  color: $dark-all-font-color;
                                }
                              }
                            }
                          }
                        }
                      }
                    }
                  }

                  .left-arrow,
                  .right-arrow {
                    background-color: $dark-card-background;
                    color: $dark-all-font-color;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
