/**=====================
  53. Dashboard CSS Start
==========================**/
.dash-breadcrumb {
  .breadcrumb-sec {
    .breadcrumb {
      justify-content: flex-end;
    }
  }
}
.options-main {
  margin-bottom: -24px;
  .option-box {
    padding: 20px 15px;
    text-align: center;
    border: 1px solid $light-gray;
    margin-bottom: 24px;
    i {
      font-size: 35px;
      margin-bottom: 10px;
      display: inline-block;
    }
    h5 {
      font-size: 16px;
      margin-bottom: 0;
    }
    h6 {
      color: $light-text;
      margin-top: 5px;
      font-size: 12px;
      margin-bottom: 0;
    }
  }
}
.default-dash,
.ecommerce-dash,
.project-dash {
  p {
    font-family: $font-roboto;
  }
  .center-content {
    display: flex;
    p {
      font-size: 12px;
      color: $light-text;
      margin-bottom: 0;
    }
    span {
      font-weight: bold;
      margin-left: 10px;
    }
    .center-content-left,
    .center-content-right {
      @include flex(flex, $align-items: center);
    }
    .center-content-right {
      border-left: 1px solid $light-gray;
      padding-left: 20px;
      margin-left: 20px;
    }
  }
  .card {
    .card-header {
      @include flex(flex, center);
      h5 {
        font-size: 16px;
        font-weight: 700;
        margin-bottom: 0px !important;
      }
    }
  }
  .media {
    + .media {
      margin-top: 20px;
      margin-bottom: 8px;
    }
  }
}

//Daily Earning section
.daily-earning-sec {
  .round-tag {
    background-color: $primary-color;
    color: $white;
    width: 25px;
    height: 25px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    align-items: center;
    justify-content: center;

    i {
      font-size: 12px;
    }
  }

  h3 {
    font-size: 26px;
    font-weight: 700;
  }
}

.tag-section {
  p {
    font-size: 12px;
    color: $light-text;
    font-weight: 500;
    margin-left: 10px;
  }

  span {
    font-weight: bold;
    color: $primary-color;
    margin-left: 10px;
  }
}

.progress-gradient-fill {
  margin: 15px 0px !important;

  .progress {
    .progress-gradient-primary {
      background-image: linear-gradient(
        to right,
        $primary-color,
        $primary-color
      );
      position: relative;
      border-radius: 5px;

      &:after {
        display: none;
      }
    }
  }
}

.sm-progress-bar {
  height: 5px;
}

.progress-animate {
  overflow: inherit;
  background-color: transparent;
  margin-bottom: 2px;
}

//Daily erning section
#chart-earning {
  max-width: 650px;
  margin: -60px -40px -29px;
}

.list-group-item {
  border-color: $light-semi-gray;
}

.list-group-item-action {
  &:hover,
  &:focus {
    background-color: rgba($primary-color, 0.1);
    color: $primary-color;
  }
}

.list-group-item-action {
  &.active {
    &:hover,
    &:focus {
      background-color: $primary-color;
      color: $white;
    }
  }
}

.dashboard-chat {
  .chat {
    .media {
      &:last-child {
        margin-bottom: 0;
      }
      .media-body {
        .message-main {
          p {
            font-size: 12px;
            padding: 12px;
            // background-color: lighten($primary-light, 7%);
            background-color: rgba($primary-color, 0.05);
            border-radius: 8px;
            margin-bottom: 10px;
            text-align: left;
            width: calc(100% - 70px);
            position: relative;
            color: $semi-dark;
          }
          &.smiley-bg {
            padding: 12px 12px;
            background-color: $primary-light;
            width: 6%;
            border-radius: 8px;
            text-align: center;
          }
        }
      }
    }

    .left-side-chat {
      .media-body {
        p {
          &:before {
            content: "";
            position: absolute;
            top: 19px;
            right: 100%;
            border: 7px solid transparent;
            border-right: 7px solid rgba($primary-color, 0.05);
          }
        }
      }
    }

    .right-side-chat {
      .media-body {
        p {
          &:before {
            content: "";
            position: absolute;
            top: 19px;
            left: 100%;
            border: 7px solid transparent;
            border-left: 7px solid rgba($primary-color, 0.05);
          }
        }
      }
    }
  }
}
.chat-footer {
  padding: 10px;
  align-items: center;
  margin-top: 15px;
  .imogi {
    margin-right: 10px;
  }
  .form-control {
    font-size: 14px;
  }
  svg {
    width: 22px;
    height: 22px;
    color: $white;
    margin-left: 10px;
  }
}

//wellcome section
.profile-greeting {
  @include back-img(url("../images/dashboard/bg.jpg"));
  color: $white;
  height: 483px;
  overflow: hidden;
  .card-body {
    z-index: 2;
  }
  h1 {
    font-size: 50px;
    font-family: $font-amanda;
    line-height: 1;
  }
  h3 {
    color: $white !important;
  }
  @keyframes zoom {
    0% {
      transform: translate(0px, 0px);
    }

    50% {
      transform: translate(10px, 10px);
    }

    100% {
      transform: translate(0px, 0px);
    }
  }
  .card-decore {
    .cross {
      transform: rotate(120deg);
      position: absolute;

      &:before {
        content: "";
        position: absolute;
        background: rgba($white, 0.8);
        border-radius: 25px;
      }

      &:after {
        content: "";
        position: absolute;
        background: rgba($white, 0.8);
        border-radius: 25px;
        transform: rotate(90deg);
      }

      &.cross1 {
        left: 140px;
        top: 50px;
        animation: zoom 10s infinite linear;

        &:before {
          left: 50px;
          @include square(15px, 4px);
        }

        &:after {
          left: 50px;
          @include square(15px, 4px);
        }
      }

      &.cross2 {
        right: 20px;
        bottom: 100px;
        animation: zoom 10s infinite linear;

        &:before {
          left: 50px;
          @include square(12px, 2px);
        }

        &:after {
          left: 50px;
          @include square(12px, 2px);
        }
      }

      &.cross3 {
        left: 30%;
        bottom: 50px;
        animation: zoom 5s infinite linear;

        &:before {
          left: 50px;
          @include square(12px, 2px);
        }

        &:after {
          left: 50px;
          @include square(12px, 2px);
        }
      }
    }

    .main {
      position: absolute;
      top: 130px;
      left: -20px;
    }

    .dot {
      position: absolute;
      background: rgba($white, 0.8);
    }

    .dot1 {
      @include square(6px, 6px, 50%);
      top: 130px;
      right: 151px;
      animation: zoom 15s infinite linear;
    }

    .dot2 {
      @include square(10px, 10px, 50%);
      top: 50px;
      right: 100px;
      animation: zoom 5s infinite linear;
    }

    .dot3 {
      @include square(8px, 8px, 50%);
      top: 160px;
      right: 50px;
      animation: zoom 10s infinite linear;
    }

    .dot4 {
      @include square(5px, 5px, 50%);
      bottom: 160px;
      right: 75px;
      animation: zoom 2s infinite linear;
    }
  }

  .btn {
    padding: 10px 30px;
    font-weight: 700;
    font-size: 16px;
    border-radius: 0;
    background-color: $white !important;
  }
  p {
    color: rgba($white, 0.6);
  }
  .badge {
    position: absolute;
    top: 40px;
    right: 40px;
    color: $white;
    font-size: 16px;
    padding: 10px 12px;
    border-radius: 10px;
    background-color: rgba($white, 0.2);
  }

  canvas {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }

  .greeting-user {
    display: flex;
    align-items: center;
    height: 100%;
  }

  .greeting-user {
    .btn {
      font-size: 14px;
      color: $primary-color;
      font-weight: bold;
    }
    p {
      text-transform: capitalize;
      color: rgba($white, 0.9);
      margin-bottom: 30px;
      line-height: 1.8;
      margin-top: 15px;
      padding: 0 15px;
    }
  }
}

/**=====================
53. Dashboard CSS Ends
==========================**/
.apex-chart {
  margin: 0 auto;
}

.apexcharts-menu-item {
