}

.chart-block {
  #our-growth {
    height: 150px;
    filter: drop-shadow(5px 5px 5px rgba(255, 83, 19, 0.22));
  }
}

@keyframes dashoffset-seven {
  0% {
    stroke-dashoffset: 7px;
  }

  100% {
    stroke-dashoffset: 0px;
  }
}

// .call-chat-card{
.rounded-circle {
  border-radius: 50%;
}

// profile sec
.profile-sec {
  .user-details-main {
    position: relative;
    text-align: center;
    width: 100%;
    .user-img {
      margin-bottom: 15px;
      img {
        border: 12px solid rgba($primary-color, 0.2);
      }
    }
    span {
      &.badge {
        position: absolute;
        top: 45%;
        left: 45%;
        display: inline-block;
        padding: 3px 5px;
        font-size: 10px;
        font-weight: 700;
        line-height: 1;
        text-align: center;
        white-space: nowrap;
        vertical-align: baseline;
        border-radius: 0.25rem;
      }
    }
    .user-detail {
      span {
        font-size: 14px;
        font-weight: 500;
        color: $theme-body-font-color;
      }
      p {
        color: $light-text;
      }
    }
  }

  //profile-sec
  .user-content {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    margin-top: 40px;
    .small-bar {
      position: relative;
      .flot-chart-container {
        @include square(60px, 150px);
        .chartist-tooltip {
          position: absolute;
          .chartist-tooltip-value {
            font-size: 10px;
            font-weight: 500;
            padding: 5px;
            color: $white;
            background-color: rgba(0, 0, 0, 0.5);
            border-radius: 4px;
          }
        }
      }
      .small-chart1 {
        .chartist-tooltip {
          .chartist-tooltip-value {
            background-color: $primary-color;
            box-shadow: 1px 1.732px 8px 0px rgba(77, 138, 255, 1);
