      color: $light-text;
      font-weight: 500;
    }
  }
}
.deals-sec {
  .card {
    .card-body {
      padding-bottom: 35px;
    }
    .setting-option {
      top: 15px;
      right: 15px;
    }
  }
}

.flat-badge-primary {
  border-radius: 0;
  background-color: $primary-color;
  box-shadow: -0.5px 4.33px 16px 0px rgba(251, 46, 99, 0.25);
  color: $white !important;
}

//social review section
.social-review-sec {
  .social-review-content {
    margin-bottom: 20px;
    h5 {
      font-size: 16px;
      font-weight: 600;
    }
    .star-sec-main {
      display: flex;
      align-items: center;
      h6 {
        font-size: 14px;
      }
      .star-sec {
        margin-left: 10px;
        display: flex;
        align-items: center;
        li {
          .fa-star {
            color: #f5c304;
          }
        }
      }
    }
    p {
      color: $light-text;
    }
  }

  .reviewer-profile {
    text-align: center;
    margin-left: -30px;
    margin-right: -30px;
    padding-top: 20px;
    padding-bottom: 20px;
    border-top: 1px solid $light-semi-gray;
    border-bottom: 1px solid $light-semi-gray;
    li {
      display: inline-block;

      img {
        width: 30px;
        transition: all 0.5s ease;
        &:hover {
          transform: scale(1.1);
        }
      }
      &:nth-child(n + 2) {
        margin-left: 10px;
      }
    }
  }

  .review-details {
    display: flex;
    justify-content: center;
    li {
      padding-top: 20px;
      padding-bottom: 20px;
      text-transform: capitalize;
      text-align: center;
      p {
        color: $light-text;
      }
      &:nth-child(2) {
        padding-left: 30px;
        margin-left: 30px;
        border-left: 1px solid $light-gray;
      }
      .counter {
        font-size: 16px;
        font-weight: 700;
      }
    }
  }
}

// statistics-sec
.statistics-sec {
  .card-footer {
    padding: 15px !important;
    border-top: 1px solid $light-gray;
  }
  .statistics-details {
    table {
      td {
        h6 {
          font-size: 12px;
          margin-bottom: 0;
        }
      }
    }
  }
}

//latest-update-sec
.latest-update-sec {
  .update-block {
    table {
      tbody {
        tr {
          td {
            padding-left: 0;
            padding-right: 0;
            .icon {
              width: 35px;
              height: 35px;
              border-radius: 50%;
              display: flex;
              align-items: center;
              justify-content: center;
              transition: all 0.5s ease;

              i {
                font-size: 18px;
                transition: all 0.5s ease;
              }

              &.bg-primary {
                background-color: rgba($primary-color, 0.1) !important;

                i {
                  color: $primary-color;
                }
              }

              &.bg-secondary {
                background-color: rgba($secondary-color, 0.1) !important;

                i {
                  color: $secondary-color;
                }
              }
            }
            &:first-child {
              min-width: 50px;
              width: 50px;
              display: block;
            }
            &:nth-child(2) {
              min-width: 270px;
            }
            &:last-child {
              min-width: 100px;
              text-align: right;
            }
            p {
              color: $semi-dark;
              font-size: 12px;
            }
          }
          &:first-child {
            td {
              padding-top: 0;
            }
          }
          &:last-child {
            td {
              padding-bottom: 0;
            }
          }
          &:hover {
            .icon {
              &.bg-primary {
                background-color: $primary-color !important;

                i {
                  color: $white;
                }
              }

              &.bg-secondary {
                background-color: $secondary-color !important;

                i {
                  color: $white;
                }
              }
            }
          }
        }
      }
    }
  }
}

//deals section
.best-dealer-table {
  table {
    thead {
      tr {
        th {
          padding-top: 0px;
          font-size: 16px;
          font-weight: bold;
        }
      }
    }
    tr {
      th,
      td {
        padding: 19px 10px;
        &:first-child {
          padding-left: 0;
        }
        &:last-child {
          padding-right: 0;
        }
      }
    }
    tbody {
      tr {
        td {
          vertical-align: middle;
          transition: 0.5s;
          position: relative;
          color: $theme-body-font-color;
          .t-title {
            display: flex !important;
            align-items: center;
          }
          .flag-icon {
            font-size: 18px;
            position: relative;
            display: inline-block;
            width: 1.33333em;
            line-height: 1em;
          }
          p {
            color: $light-text;
            transition: 0.5s;
            font-size: 12px;
          }
          .label {
            padding: 8px 12px;
            color: $theme-body-font-color;
            border-radius: 10px;
            transition: 0.5s;
            font-size: 13px;
          }
          .align-middle {
            position: relative;

            .status-circle {
              @include square(10px, 10px);
              top: 2px;
              left: 32px;
              opacity: 0;
              transition: 0.5s;
            }
          }
        }

        &:hover {
          td {
            color: $primary-color;
            transition: 0.5s;
            p {
              color: $primary-color;
              transition: 0.5s;
            }
            span {
              color: $black;
              transition: 0.5s;
            }
            .label {
              background-color: rgba($primary-color, 0.1);
              color: $primary-color;
              transition: 0.5s;
            }
            .align-middle {
              .status-circle {
                opacity: 1;
                transition: 0.5s;
              }
            }
          }
        }

        &:last-child {
          td {
            padding-bottom: 0;
          }
        }
      }
    }
  }
}

//Goal overview section
.goal-overview-sec {
  .card-footer {
    padding: 0 !important;
    border-top: 1px solid $light-semi-gray;
  }
  .goal-details {
    display: flex;
    justify-content: space-around;
    li {
      padding-top: 36px;
      padding-bottom: 36px;
      width: 100%;
      text-align: center;
      &:nth-child(n + 2) {
        border-left: 1px solid $light-gray;
      }
    }
  }
  .goal-chart-block {
    .goal-overview-chart {
      path {
        stroke-width: 10px;
        stroke-dasharray: 10, 20;
        stroke-linecap: round;
      }

      #apexcharts-radialbarTrack-0 {
        stroke: $primary-color;
        opacity: 0.3;

        stroke-linecap: round;
      }
    }

    .highest-goal {
      position: absolute;
      right: 3%;
      top: 15%;
      padding: 7px 18px;
      line-height: 1;
      background: $white;
      box-shadow: 0 0 47px 10px rgba($primary-color, 0.1);

      span {
        font-weight: bold;
        color: $primary-color;
      }

      p {
        font-size: 10px;
        font-weight: 500;
      }
    }
  }
  .goal-details {
    h4 {
      font-size: 18px;
      font-weight: bold;
      color: $primary-color;
    }
  }
}

//help section
.help-sec {
  @include back-img(url("../images/dashboard/help.jpg"));
  height: 575px;
  .card-header {
    background-color: transparent;
  }
  .round-animation {
    i {
      background: $light-semi-gray;
      top: 15%;
      right: 47%;
      box-shadow: 0 15px 30px 0 $light-semi-gray;
      position: absolute;
      @include square(30px, 30px, 100%);
      opacity: 0.3;
      transform: scale(1.3);
      animation: ripple1 3s linear infinite;

      &:nth-child(2) {
        animation: ripple2 3s linear infinite;
      }
    }
  }
  .help-desk {
    margin: 0;
    width: 70%;
    position: absolute;
    top: 50%;
    left: 50%;
    margin-right: -50%;
    transform: translate(-50%, -50%);
    .help-profile {
      padding-bottom: 90px;
    }
    h4 {
      font-weight: 600;
      margin-bottom: 20px;
    }
    p {
      margin-bottom: 0;
    }
    .btn {
      font-size: 14px;
      color: $primary-color;
      margin-top: 30px;
      border-radius: 0 !important;
    }
  }
  .badge-main {
    @include flex(flex, center, flex-end);
    .badge {
      color: $white !important;
      padding: 10px 14px !important;
      background: linear-gradient(
        90deg,
        rgba($white, 0.1) 20%,
        rgba(0, 0, 0, 0) 100%
      );
      border-radius: 5px;
      width: 56px;
      text-align: left;
    }
  }
}

@keyframes ripple1 {
  0% {
    transform: scale(5.5);
    opacity: 0.3;
  }

  100% {
    transform: scale(8.5);
    opacity: 0;
  }
}

@keyframes ripple2 {
  0% {
    transform: scale(3.5);
  }

  100% {
    transform: scale(5.5);
  }
}

.apex-complain {
  margin-bottom: -19px;
}

.small-mix {
  opacity: 0.3;

  svg {
    position: absolute;
    bottom: 0;
    left: 0;
    height: 90px !important;
  }
}

//general widget css start
