/**=====================
     74. internationalization css start
==========================**/
.hdg_main {
  position: relative;
}

.main {
  .langChoice {
    position: absolute;
    right: 30px;
    top: 20px;
    padding: 10px 12px;
    background-color: rgba($primary-color, 0.05);
    border-color: $light-gray;
    color: $primary-color;
  }
}

// responsive css 
@media (max-width: 991px) {
  .main {
    .langChoice {
      right: 20px;
      top: 11px;
      padding: 8px;
    }
  }
}

@media only screen and (max-width: 575px) {
  .main {
    .langChoice {
      top: 14px;
      padding: 4px 7px;
    }
  }

  .language-xs {
    margin-top: 15px;
  }
}

@media only screen and (max-width: 360px) {
  .language-header {
    padding-bottom: 60px !important;
  }

  .main {
    .langChoice {
      left: 50%;
      margin: 0 auto;
      top: 50px;
      transform: translateX(-50%);
    }
  }
}

/**=====================
     74. internationalization CSS end
==========================**/