/**=====================
    60. RTL CSS Start
==========================**/
.rtl {

  table.dataTable tbody {
    td.select-checkbox:after {
      display: block;
      position: absolute;
      top: 1.6em;
      left: 28%;
      width: 12px;
      height: 12px;
      box-sizing: border-box;
    }
  }

  .checkbox-check {
    table.dataTable tbody td.select-checkbox:before {
      right: 15px;
      top: 22px;
      left: unset;
    }

    table.dataTable tbody {
      td.select-checkbox:after {
        display: block;
        position: absolute;
        top: 1.6em;
        left: 84%;
        width: 12px;
        height: 12px;
        box-sizing: border-box;
      }
    }
  }

  ul {
    padding-right: 0;
  }

  .f-right {
    float: left;
  }

  .form-check-inline {
    margin-right: unset;
    margin-left: 1rem;
  }

  .text-lg-start {
    text-align: right !important;
  }

  .page-wrapper {
    &.compact-wrapper {
      .page-body-wrapper {
        header {
          &.main-nav {
            position: fixed;
            border-bottom: none;
            z-index: 9;
            height: auto;
            line-height: inherit;
            background: $white;
            width: 280px;
            text-align: right;
            transition: 0.5s;
            border-right: unset;
            border-left: 1px solid $light-gray;

            &.close_icon {
              margin-right: 0;
              margin-left: unset;
              transform: translate(280px);

              .logo-icon-wrapper {
                text-align: left;

                img {
                  margin-left: 8px;
                }
              }

              ~.page-body,
              ~footer {
                margin-left: 0;
                margin-right: 0;
                transition: 0.5s;
              }

              .main-navbar {
                .nav-menu {
                  >li {
                    a {
                      svg {
                        float: left;
                        margin-left: 0;
                      }
                    }
                  }

                  .dropdown {
                    .menu-title {
                      &.active {
                        ~.menu-content {
                          display: none !important;
                        }
                      }
                    }
                  }
                }
              }

              .according-menu {
                display: none;
              }

              &:hover {
                .main-navbar {
                  .nav-menu {
                    >li {
                      a {
                        svg {
                          margin-left: 13px;
                        }
                      }
                    }
                  }
                }
              }
            }

            &:hover {
              transform: translate(0px);

              .main-navbar {
                .nav-menu {
                  .dropdown {
                    .menu-title {
                      &.active {
                        ~.menu-content {
                          display: block !important;
                        }
                      }
                    }
                  }

                  >li {
                    a {
                      svg {
                        float: none;
                      }
                    }
                  }
                }
              }

              .according-menu {
                display: block;
              }
            }

            .main-navbar {
              .nav-menu {
                >li {
                  .nav-submenu {
                    &:after {
                      right: 30px;
                      left: unset;
                    }

                    >li {
                      a {
                        padding-right: 45px !important;
                        padding-left: unset !important;

                        &:after {
                          right: 26px;
                          left: unset;
                        }
                      }
                    }
                  }
                }
              }
            }
          }
        }

        .page-body {
          margin-left: unset;
          margin-right: 280px;
        }

        .main-nav {
          text-align: right;

          .main-navbar {
            .nav-menu {
              >li {
                a {
                  svg {
                    margin-right: unset;
                    margin-left: 15px;
                  }
                }
              }
            }
          }
        }

        .according-menu {
          right: unset;
          left: 20px;
        }
      }
    }
  }

